@tailwind base;
@tailwind components;
@tailwind utilities;

/* X App Global Styles */
@layer base {
  body {
    @apply bg-x-black text-x-white font-x;
    margin: 0;
    font-family: "Helvetica Neue", Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  * {
    border-color: #2f3336;
  }
}

/* Enable smooth scrolling for all viewports */
html {
  scroll-behavior: smooth;
}

/* Dropdown option styling */
.language-dropdown option {
  background: #16181c;
  color: #e7e9ea;
  padding: 8px 12px;
  font-family: "Monaco", "Menlo", "Ubuntu Mono", monospace;
}

/* Enhanced code snippet styling */
.code-snippet {
  background: linear-gradient(135deg, #0d1117 0%, #161b22 100%);
  border: 1px solid #30363d;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
}

.code-snippet pre {
  background: transparent;
  margin: 0;
  padding: 16px;
  font-family: "Monaco", "Menlo", "Ubuntu Mono", "Courier New", monospace;
  color: #58a6ff;
  text-shadow: 0 0 2px rgba(88, 166, 255, 0.3);
}

/* Syntax highlighting colors */
.code-snippet .comment {
  color: #8b949e;
}
.code-snippet .keyword {
  color: #ff7b72;
}
.code-snippet .string {
  color: #a5d6ff;
}
.code-snippet .number {
  color: #79c0ff;
}
.code-snippet .function {
  color: #d2a8ff;
}

/* X App Custom Components */
@layer components {
  .x-container {
    @apply max-w-6xl mx-auto flex min-h-screen;
  }

  /* Mobile-friendly container */
  .x-container-mobile {
    @apply w-full lg:max-w-6xl lg:mx-auto flex items-stretch flex-1;
  }

  .x-sidebar {
    @apply w-64 p-4 border-r border-x-border;
  }

  /* Mobile-friendly sidebar */
  .x-sidebar-mobile {
    @apply w-16 lg:w-64 p-2 lg:p-4 border-r border-x-border fixed bottom-0 lg:bottom-auto left-0 lg:left-auto bg-x-black lg:bg-transparent z-40 lg:z-auto h-screen lg:h-full overflow-y-auto;
  }

  .x-main {
    @apply flex-1 max-w-x-main border-r border-x-border;
  }

  /* Mobile-friendly main content */
  .x-main-mobile {
    @apply flex-1 max-w-none lg:max-w-x-main lg:border-r lg:border-x-border w-full ml-0 lg:ml-64 px-2 lg:px-0;
  }

  .x-rightbar {
    @apply w-80 p-4 border-l border-x-border;
  }

  .x-post {
    @apply border-b border-x-border p-4 hover:bg-x-darker transition-colors cursor-pointer;
  }

  .x-button {
    @apply bg-x-blue hover:bg-x-blue-hover text-white font-bold py-4 px-8 rounded-full transition-all duration-200 transform hover:scale-105 shadow-lg hover:shadow-xl;
  }

  .x-button-outline {
    @apply border-2 border-x-blue text-x-blue hover:bg-x-blue hover:text-white font-bold py-4 px-8 rounded-full transition-all duration-200 transform hover:scale-105 hover:shadow-lg;
  }

  .x-input {
    @apply bg-x-dark border border-x-border rounded-lg px-4 py-3 text-x-white placeholder-x-gray focus:border-x-blue focus:outline-none transition-colors;
  }

  .x-card {
    @apply bg-x-dark border border-x-border rounded-2xl p-6 backdrop-blur;
  }

  /* Custom button styles */
  .btn-primary {
    @apply bg-x-blue hover:bg-x-blue-hover text-white font-medium py-2 px-4 rounded-lg transition-colors duration-200;
  }

  /* Text color classes */
  .text-primary {
    @apply text-x-white;
  }

  .text-secondary {
    @apply text-x-gray;
  }

  .text-accent {
    @apply text-x-blue;
  }

  /* Custom animations */
  .animate-float {
    animation: float 6s ease-in-out infinite;
  }

  .animate-float-delayed {
    animation: float 6s ease-in-out infinite;
    animation-delay: 2s;
  }

  .animate-float-slow {
    animation: float 8s ease-in-out infinite;
    animation-delay: 1s;
  }

  .animate-like-pop {
    animation: like-pop 0.6s cubic-bezier(0.23, 1, 0.32, 1);
  }

  @keyframes like-pop {
    0% {
      transform: scale(1);
    }
    30% {
      transform: scale(1.3);
    }
    60% {
      transform: scale(0.9);
    }
    100% {
      transform: scale(1);
    }
  }

  .animate-like-burst {
    animation: like-burst 0.6s cubic-bezier(0.23, 1, 0.32, 1);
  }

  @keyframes like-burst {
    0% {
      opacity: 0.7;
      transform: scale(0.5);
    }
    60% {
      opacity: 0.3;
      transform: scale(1.2);
    }
    100% {
      opacity: 0;
      transform: scale(1.8);
    }
  }

  /* Follow button animation */
  .animate-follow-pop {
    animation: follow-pop 0.35s cubic-bezier(0.23, 1, 0.32, 1);
  }

  @keyframes follow-pop {
    0% {
      transform: scale(1);
    }
    40% {
      transform: scale(1.18);
    }
    60% {
      transform: scale(0.95);
    }
    100% {
      transform: scale(1);
    }
  }

  /* DevMate text animation */
  @keyframes devmate-gradient {
    0%,
    100% {
      color: #fff;
    }
    50% {
      color: #3b82f6; /* Tailwind x-blue */
    }
  }

  .animate-devmate-gradient {
    animation: devmate-gradient 2s linear infinite;
    /* Remove pulse or scale for a smooth color-only transition */
  }

  @keyframes dev-quote-fade {
    0%,
    100% {
      opacity: 1;
    }
    45%,
    55% {
      opacity: 0;
    }
  }

  .animate-dev-quote-fade {
    animation: dev-quote-fade 4s linear infinite;
  }
}

/* Keyframes */
@keyframes float {
  0%,
  100% {
    transform: translateY(0px) rotate(0deg);
  }
  50% {
    transform: translateY(-10px) rotate(1deg);
  }
}

/* Scrollbar Styling */
::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-track {
  background: #16181c;
}

::-webkit-scrollbar-thumb {
  background: #2f3336;
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: #71767b;
}

/* Gradient text effect */
.gradient-text {
  background: linear-gradient(45deg, #1d9bf0, #00ba7c);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Animated gradient text for CTA */
.animated-gradient-text {
  background: linear-gradient(
    270deg,
    #3b82f6,
    #10b981,
    #f59e42,
    #ec4899,
    #3b82f6
  );
  background-size: 1000% 1000%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
  animation: animatedGradient 4s ease-in-out infinite;
}

@keyframes animatedGradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* Rainbow text effect */
.rainbow-text {
  background: linear-gradient(90deg, #ff6ec4, #7873f5, #42e695, #ff6ec4);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: transparent;
  animation: rainbow-move 2s linear infinite;
}

@keyframes rainbow-move {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 100% 50%;
  }
}

/* Mobile-specific improvements */
@media (max-width: 768px) {
  .btn-primary {
    @apply text-sm py-2 px-3;
  }

  .card {
    @apply rounded-lg mx-1;
  }

  /* Ensure mobile menu appears above all content */
  .mobile-menu {
    position: fixed;
    top: 64px; /* Height of navbar */
    left: 0;
    right: 0;
    z-index: 9999;
  }

  /* Full width developer cards on mobile */
  .developer-card {
    margin-left: 0 !important;
    margin-right: 0 !important;
    width: 100% !important;
  }

  /* Better mobile alignment for profile cards */
  .profile-header-mobile {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 0.75rem;
  }

  .profile-header-mobile .profile-info {
    flex: 1;
    min-width: 0;
  }

  .profile-header-mobile .profile-actions {
    flex-shrink: 0;
  }
}

/* Improved mobile navigation spacing */
@media (max-width: 1024px) {
  .x-main-mobile {
    padding-bottom: 80px; /* Space for floating action button */
  }
}

/* Floating Post Button Enhanced Styles */
.floating-post-btn {
  background: linear-gradient(135deg, #1d9bf0 0%, #1a8cd8 50%, #0d7377 100%);
  box-shadow: 0 8px 32px rgba(29, 155, 240, 0.3);
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 255, 255, 0.1);
  position: fixed !important;
  z-index: 999 !important;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  transform-origin: center;
}

.floating-post-btn:hover {
  background: linear-gradient(135deg, #1a8cd8 0%, #1d9bf0 50%, #0d7377 100%);
  box-shadow: 0 12px 40px rgba(29, 155, 240, 0.4);
  transform: scale(1.1) rotate(5deg);
  animation: float-pulse 2s ease-in-out infinite;
}

.floating-post-btn:active {
  transform: scale(0.95);
}

@keyframes float-pulse {
  0%,
  100% {
    box-shadow: 0 8px 32px rgba(29, 155, 240, 0.3);
  }
  50% {
    box-shadow: 0 12px 40px rgba(29, 155, 240, 0.5);
  }
}

/* Enhanced button positioning above footer */
@media (max-width: 1024px) {
  .floating-post-btn {
    position: fixed !important;
    bottom: 80px !important; /* Above footer */
    right: 1rem !important;
    z-index: 999 !important;
    will-change: transform;
  }
}

/* Scroll-based visibility animations */
.floating-post-btn-container {
  transform: translateY(0);
  opacity: 1;
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.floating-post-btn-container.hidden {
  transform: translateY(100px);
  opacity: 0;
  pointer-events: none;
}

.floating-post-btn-container.visible {
  transform: translateY(0);
  opacity: 1;
  animation: bounceIn 0.6s ease-out;
}

@keyframes bounceIn {
  0% {
    transform: translateY(100px) scale(0.3);
    opacity: 0;
  }
  50% {
    transform: translateY(-10px) scale(1.05);
    opacity: 0.8;
  }
  70% {
    transform: translateY(5px) scale(0.95);
    opacity: 0.9;
  }
  100% {
    transform: translateY(0) scale(1);
    opacity: 1;
  }
}

/* Custom styling for language dropdown */
.language-dropdown {
  background: linear-gradient(135deg, #16181c 0%, #1e2328 100%);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(47, 51, 54, 0.5);
  transition: all 0.3s ease;
}

.language-dropdown:hover {
  background: linear-gradient(135deg, #1e2328 0%, #16181c 100%);
  border-color: rgba(29, 155, 240, 0.3);
  box-shadow: 0 4px 20px rgba(29, 155, 240, 0.1);
}

.language-dropdown:focus {
  background: linear-gradient(135deg, #1e2328 0%, #16181c 100%);
  border-color: #1d9bf0;
  box-shadow: 0 0 0 2px rgba(29, 155, 240, 0.2);
}

/* Line clamp utility for text truncation */
.line-clamp-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.line-clamp-3 {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

/* Shimmer animation */
@keyframes shimmer {
  0% {
    background-position: -200px 0;
    opacity: 0.6;
  }
  50% {
    opacity: 0.9;
  }
  100% {
    background-position: 200px 0;
    opacity: 0.6;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-shimmer {
  animation: shimmer 1.8s ease-in-out infinite;
  background: linear-gradient(
    90deg,
    #1a1a1a 0%,
    #262626 25%,
    #353535 50%,
    #262626 75%,
    #1a1a1a 100%
  );
  background-size: 400px 100%;
  transition: all 0.3s ease-in-out;
}

.animate-fade-in {
  animation: fade-in 0.5s ease-out;
}

/* Mobile menu overlay animation */
.mobile-menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(6px);
  z-index: 40;
  animation: fadeInOverlay 0.3s ease-out;
}

.mobile-menu-content {
  position: fixed;
  top: 64px; /* Below navbar */
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 50;
  animation: slideDownMenu 0.3s ease-out;
  overflow-y: auto;
}

/* Ensure navbar stays above overlay */
nav {
  position: relative !important;
  z-index: 60 !important;
}

/* Force navbar to always be fixed at the top, no matter what */
nav[role="navigation"],
nav.fixed,
nav.z-\[60\] {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  width: 100vw !important;
  z-index: 9999 !important;
}

@keyframes fadeInOverlay {
  from {
    opacity: 0;
    backdrop-filter: blur(0px);
  }
  to {
    opacity: 1;
    backdrop-filter: blur(6px);
  }
}

@keyframes slideDownMenu {
  from {
    transform: translateY(-100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Bottom Navigation Styles - Enhanced Glassmorphism */
.bottom-nav-pill {
  background: rgba(22, 24, 28, 0.15);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3), 0 0 0 1px rgba(255, 255, 255, 0.05),
    inset 0 1px 0 rgba(255, 255, 255, 0.1), inset 0 -1px 0 rgba(0, 0, 0, 0.2);
  position: relative;
  overflow: hidden;
}

.bottom-nav-pill::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.1),
    transparent
  );
  transition: left 0.5s ease;
}

.bottom-nav-pill:hover::before {
  left: 100%;
}

.bottom-nav-item {
  position: relative;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

.bottom-nav-item:hover {
  transform: translateY(-2px);
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);
}

.bottom-nav-item.active {
  transform: scale(1.1);
  background: rgba(29, 155, 240, 0.2);
  box-shadow: 0 0 20px rgba(29, 155, 240, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.bottom-nav-item::before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: 50%;
  padding: 1px;
  background: linear-gradient(
    135deg,
    rgba(29, 155, 240, 0.5),
    rgba(0, 186, 124, 0.5)
  );
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  mask-composite: xor;
  -webkit-mask-composite: xor;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.bottom-nav-item.active::before {
  opacity: 1;
}

/* Enhanced special button for create post */
.bottom-nav-special {
  background: linear-gradient(
    135deg,
    rgba(29, 155, 240, 0.8) 0%,
    rgba(26, 140, 216, 0.8) 50%,
    rgba(0, 186, 124, 0.8) 100%
  );
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  box-shadow: 0 4px 20px rgba(29, 155, 240, 0.4),
    0 0 0 1px rgba(255, 255, 255, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.bottom-nav-special:hover {
  background: linear-gradient(
    135deg,
    rgba(29, 155, 240, 0.9) 0%,
    rgba(26, 140, 216, 0.9) 50%,
    rgba(0, 186, 124, 0.9) 100%
  );
  box-shadow: 0 6px 24px rgba(29, 155, 240, 0.5),
    0 0 0 1px rgba(255, 255, 255, 0.3), inset 0 1px 0 rgba(255, 255, 255, 0.4);
  transform: translateY(-2px) scale(1.05);
}

/* Scroll-based reveal animation */
@keyframes bottomNavReveal {
  from {
    transform: translateY(100px) scale(0.8);
    opacity: 0;
  }
  to {
    transform: translateY(0) scale(1);
    opacity: 1;
  }
}

/* Smooth fade-in-out for Copied message */
@keyframes fadeInOut {
  0% {
    opacity: 0;
    transform: translateY(-4px);
  }
  10% {
    opacity: 1;
    transform: translateY(0);
  }
  90% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(-4px);
  }
}
.animate-fade-in-out {
  animation: fadeInOut 1.2s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Remove focus ring from copy button when copied */
.copy-btn:focus:not(:focus-visible) {
  outline: none;
  box-shadow: none;
}
